import React, { Component } from 'react';
import { keyBy, transform } from 'lodash';
import axios from 'axios';
import './App.css';
// import result from './data';
import Header from './components/Header';
import WeatherTable from './components/WeatherTable';

/**
 * Change this with your tocken
 */
const TOKEN = 'f630954b291bc6839e43ab75daa7201f';

const RESTAPI = `https://api.openweathermap.org/data/2.5/forecast?q=Cluj-Napoca&units=metric&appid=${TOKEN}`;
class App extends Component {
  state = {
    data: null,
    units: 'metric',
  };

  async getData() {
    try {
      const result = await axios(RESTAPI);
      this.setState({ data: result.data });
    } catch (err) {
      new Error(err);
    }
  }
  handleChangeMetric = e => {
    this.setState({ units: e.target.value });
  };
  componentDidMount = () => {
    this.getData();
  };

  render() {
    if (!this.state.data) {
      return <div>Loading ....</div>;
    }
    const { city, list } = this.state.data;
    //arrange the list of data by date
    let arrangeList = keyBy(list, 'dt_txt');
    let transformedList = transform(
      arrangeList,
      (result, value, key) => {
        key = key.split(' ');
        (result[key[0]] || (result[key[0]] = [])).push(value);
      },
      {},
    );

    return (
      <div>
        <Header
          city={city.name}
          units={this.state.units}
          todaysTemp={list[0]}
          changeMetric={this.handleChangeMetric}
        />
        <div>
          <WeatherTable units={this.state.units} data={transformedList} />
        </div>
      </div>
    );
  }
}

export default App;



