import React from 'react';

export default ({ data, units }) => {
  const { humidity, icon, pressure, description, temp, wind } = data;
  let tempSimbol = units === 'metric' ? 'C' : 'F';
  return (
    <div style={{ height: '65px', fontSize: '12px' }}>
      <span style={{ textTransform: 'capitalize' }}>{description} </span>{' '}
      <img
        style={{
          float: 'right',
          display: 'block',
          boxShadow: '1px 1px 1px rgba("0, 0, 0, 0.5")',
        }}
        height={40}
        src={`/images/${icon}.png`}
        alt={icon}
      />
      <div>
        Temp: {temp} <sup>o</sup> {tempSimbol}
      </div>
      <div>
        Humidity: {humidity},<br /> Wind: {wind} <br />Pressure: {pressure}
      </div>
    </div>
  );
};



