import React from 'react';
import * as moment from 'moment';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import WeatherBox from './WeatherBox';
import './style.css';

export default ({ data, units }) => {
  let result = [];
  let addTemp = units === 'metric' ? 0 : 33.8;
  Object.keys(data).map((res, index) => {
    result.push({
      day: moment(res).format('dddd'),
    });
    let dayIndex = index;
    data[res].map((dt, index) => {
      result[dayIndex]['hour' + index] = {};
      result[dayIndex]['hour' + index].temp = Math.round(
        dt.main.temp + addTemp,
      );
      result[dayIndex]['hour' + index].pressure = dt.main.pressure;
      result[dayIndex]['hour' + index].humidity = dt.main.humidity;
      result[dayIndex]['hour' + index].icon = dt.weather[0].icon;
      result[dayIndex]['hour' + index].description = dt.weather[0].description;
      result[dayIndex]['hour' + index].wind = dt.wind.speed;
      return result;
    });
    return result;
  });
  return (
    <ReactTable
      data={result}
      pageSize={5}
      showPageJump={false}
      showPagination={false}
      columns={[
        {
          Header: '5 Days',
          style: { width: '80px' },
          columns: [
            {
              Header: 'Day',
              id: 'day',
              accessor: 'day',
              className: 'currentDay',
            },
          ],
        },
        {
          Header: 'Hours',
          columns: [
            {
              Header: '00:00',
              accessor: 'hour0',
              Cell: data => <WeatherBox units={units} data={data.row.hour0} />,
            },
            {
              Header: '03:00',
              accessor: 'hour1',
              Cell: data => <WeatherBox units={units} data={data.row.hour1} />,
            },
            {
              Header: '06:00',
              accessor: 'hour2',
              Cell: data => <WeatherBox units={units} data={data.row.hour2} />,
            },
            {
              Header: '09:00',
              accessor: 'hour3',
              Cell: data => <WeatherBox units={units} data={data.row.hour3} />,
            },
            {
              Header: '12:00',
              accessor: 'hour4',
              Cell: data => <WeatherBox units={units} data={data.row.hour4} />,
            },
            {
              Header: '15:00',
              accessor: 'hour5',
              Cell: data => <WeatherBox units={units} data={data.row.hour5} />,
            },
            {
              Header: '18:00',
              accessor: 'hour6',
              Cell: data => <WeatherBox units={units} data={data.row.hour6} />,
            },
            {
              Header: '21:00',
              accessor: 'hour7',
              Cell: data => <WeatherBox units={units} data={data.row.hour7} />,
            },
          ],
        },
      ]}
      className="-striped -highlight"
    />
  );
};



