import React from 'react';
import './style.css';

export default ({ city, units, todaysTemp, changeMetric }) => {
  return (
    <header>
      <div className="header_forecast">Header Forecast </div>
      <div className="header_city_info">
        <div className="header_city">
          <div>{city}</div>
          <div>
            <img
              style={{ width: '65px' }}
              src={`/images/${todaysTemp.weather[0].icon}.png`}
              alt={todaysTemp.weather[0].description}
            />
            <span style={{ fontSize: '10px' }}>
              {todaysTemp.weather[0].main}
            </span>
          </div>
        </div>
        <div className="header_metrics">
          <button
            className={`header_${units}`}
            value="metric"
            onClick={e => changeMetric(e)}
          >
            Celsius
          </button>
          <button
            className={`header_${units}`}
            value="imperial"
            onClick={e => changeMetric(e)}
          >
            Fahrenheit
          </button>
        </div>
      </div>
    </header>
  );
};



